$backgroundTile: "/assets/watertile.jpg";
$outlineColor: rgb(189, 189, 189);
$backgroundColor: rgb(30,30,30);
$portraitSize: 125px;
$windowMargin: 100px; //this is a bogus number, its not actual # bc of width of the top banner
$paddingText: 1rem;

//general text stylings

body{
    font-family: 'Times New Roman', Times, serif;
}

p {
    color: $outlineColor;
    padding: $paddingText;
    margin: 0;
}

a{
    color: $outlineColor;
}
//unvisited
a:link {
    color: $outlineColor;
}
//visited
a:visited {
    color: rgb(125, 125, 125);
}

#loadNew{
    color: $outlineColor;
}

//for the layout

.flexbox {
    display: flex;
    flex-wrap: nowrap;
}

.vFlex {
    @extend .flexbox;
    flex-direction: column;
}

.hFlex{
    @extend .flexbox;
    flex-direction: row;
}

//container for first layer

#layer0{
    background-color: rgb(30, 30, 30);
    background-image: url($backgroundTile);
    margin: 10px $windowMargin 50px $windowMargin;
    border: 2px solid $outlineColor;
}

//footer, else

#layer1 {
    background-color: $backgroundColor;
    @extend .vFlex;
}

#siteFooter{
    padding: 2px 1em 2px 1em;
    height: 1.2em;
    border-top: 2px solid $outlineColor;

    //for now until i do more with this
    color: $outlineColor;
}

//left and right, else

#layer2 {
    @extend .hFlex;
}

//portraits start
#bannerLeft{
    //portraits
    @extend .vFlex;
    width: $portraitSize;
    height: $portraitSize*5;
    padding: 0;
    border-right: 2px solid $outlineColor;
}

.statBox{
    @extend .hFlex;
}

#bannerRight{
    //includes topRight and bottomRight
    @extend .vFlex;
    width: $portraitSize*2;
    border-left: 2px solid $outlineColor;
}

#topRight{
    //clock and stats
    border-bottom: 2px solid $outlineColor;
    min-height: 4rem;
}

#bottomRight{
    //inventory
    flex-grow: 1;
}

//top and main content

#layer3 {
    @extend .vFlex;
    flex-grow: 1;
}

#bannerTop{
    //picture banner
    @extend .hFlex;
    height: 200px;
    width: 800px;
    background-color: rgb(50,50,50);
    border-bottom: 2px solid $outlineColor;
}

#mainContent{
    //this is an iframe
    flex-grow: 1;
    border: none;
}

#insideFrame{
    @extend .vFlex;
    background-color: $backgroundColor;
}

